// 角度转化为弧度
// @param{Number} angle
// @return{Number}
function angleToRadius(angle) {
	return (angle * Math.PI) / 180;
}
// 计算两个经纬度之间的距离
// @param{Array} lng1 一个坐标的经度
// @param{Array} lat1 一个坐标的纬度
// @param{Array} lng2 一个坐标的经度
// @param{Array} lat2 一个坐标的纬度
// @return{Number} 两个经纬度之间的距离
function calcDistance(lng1, lat1, lng2, lat2) {
	var radLat1 = angleToRadius(lat1);
	var radLat2 = angleToRadius(lat2);
	var a = radLat1 - radLat2;
	var b = angleToRadius(lng1) - angleToRadius(lng2);
	var s =
		2 *
		Math.asin(
			Math.sqrt(
				Math.pow(Math.sin(a / 2), 2) +
					Math.cos(radLat1) * Math.cos(radLat2) * Math.pow(Math.sin(b / 2), 2)
			)
		);
	// 赤道长度(m)
	s = s * 6378137;
	s = Math.round(s * 10000) / 10000;
	return s;
}
export default {
	calcDistance,
};
