<template>
	<div>
		<div class="bmap-page-container" id="container">
			<el-amap
				ref="map"
				:center="center"
				:zoom="zoom"
				@init="init"
				@click="click"
				class="bmap-demo"
			>
				<el-amap-marker :position="marker.position"></el-amap-marker>
				<el-amap-polyline :path="polyline.path"></el-amap-polyline>
			</el-amap>
		</div>
		<div>now_x: {{ center[0] }} now_y: {{ center[1] }}</div>
		<br />
		<div>上行数据</div>
		<div>
			<div>
				<input :value="center[0]" ref="loc_x" />
				<input :value="center[1]" ref="loc_y" />
				<button @click="handleUpdateLoc">enter</button>
			</div>
			<div>
				<button :disabled="!zoom" @click="handleDecreaseZoom">-</button>
				<span> {{ zoom }} </span>
				<button :disabled="zoom === 20" @click="handleIncreaseZoom">+</button>
			</div>
		</div>
		<div>行程：{{ distance }}（m）</div>
		<div>历史记录</div>
		<div>
			<div v-for="(item, index) in historyLoc" :key="index">
				(x, y): ({{ item[0] }}, {{ item[1] }})
			</div>
		</div>
	</div>
</template>

<script>
import util from "../utils/util.js";

export default {
	data() {
		return {
			map: null,
			zoom: 16,
			resizeEnable: true,
			center: [108.828811, 34.124211],
			historyLoc: [],
			distance: 0,
		};
	},
	computed: {
		marker() {
			return {
				position: this.center,
			};
		},
		polyline() {
			return {
				path: this.historyLoc,
			};
		},
	},

	mounted() {},
	methods: {
		init(o) {
			this.map = o;
			this.historyLoc.push([...this.center]);
			// this.historyLoc.push([...this.center]);
			// console.log(o.getCenter());
			// console.log(this.$refs.map.$$getInstance());
		},
		click() {
			// alert("click map");
			console.log("click map");
			console.log(this.center);
			console.log(this.map.getScale("1"));
		},
		handleUpdateLoc() {
			this.distance += util.calcDistance(
				this.$refs.loc_x.value,
				this.$refs.loc_y.value,
				...this.center
			);
			this.center = [this.$refs.loc_x.value, this.$refs.loc_y.value];
			this.historyLoc.push([...this.center]);
		},
		handleDecreaseZoom() {
			this.zoom--;
		},
		handleIncreaseZoom() {
			this.zoom++;
		},
	},
};
</script>

<style>
#container {
	width: 350px;
	height: 350px;
}
</style>
