<template>
	<div>
		<!-- <div class="container">
			<div>switch: {{ deviceSwitch }}</div>
			<div>color: {{ color }}</div>
			<div>brightness: {{ brightness }}</div>
			<button @click="handleDeviceBlink">闪灯行为</button>
			<button @click="handleControlDeviceData">控制设备属性</button>
		</div>
		<div>VueAMap</div> -->
		<VueAMap></VueAMap>
	</div>
</template>

<script>
import VueAMap from './components/VueAMap.vue';


export default {
	components: {
		VueAMap,
  },
	name: "App",
	data() {
		return {
			deviceSwitch: 1,
			color: 0,
      brightness: 1,
		};
	},
	methods: {
		// 控制设备闪灯
		handleDeviceBlink() {
			const action = "blink";
			const data = {
				period: 5,
				interval: 0.2,
			};
			console.log("call the device's action named ", action);
			this.sdk
				.callDeviceAction(data, action)
				.then((res) => {
					console.log("callDeviceAction", res);
				})
				.catch((err) => {
					console.log("callDeviceAction", err);
				});
		},

		// 控制设备属性
		handleControlDeviceData() {
			this.color = ++this.color % 3;
			this.brightness = ++this.brightness % 101;
			const data = {
				switch: this.switch,
				color: this.color,
				brightness: this.brightness,
			};
			this.sdk
				.controlDeviceData(data)
				.then((res) => {
					console.log("handleControlDeviceData res", res);
				})
				.catch((err) => {
					console.log("handleControlDeviceData err", err);
				});
		},
	},
};
</script>

